	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Yes We Can in 2036","date":"05 Nov 2008","slug":"/2008-11-05-yes-we-can-in-2036/","tags":"Y2008"},"html":"<p>Christopher followed the US election campaign quite closely, and was a staunch supporter of Obama. He kept informed with the children's news program Logo on KiKa and made sure he didn't miss any US news when they showed up on the adult news programs. He told us to wake him up this morning with the result was know, and we all three watched Obama's victory speech at 6am German time.</p>\n<p>Christopher is especially fascinated by Obama's heritage. He is aware of race is the naive way we wish children would never outgrow. Brown skin is just fine with him, since his best friend Ergin in kindergarten was Turkish and also had brown skin. The past couple of weeks we've had a mixture of brown and white eggs in our refrigerator. Christopher dubbed them Obama eggs and McCain eggs, and made sure we ate the McCain eggs first. Our egg tray is now 100% for Obama.</p>\n<p><a title=\"View 'CIMG0638' on Flickr.com\" href=\"http://www.flickr.com/photos/51035717986@N01/3005301005\"><img src=\"http://farm4.static.flickr.com/3205/3005301005_49c1a8465d_m.jpg\" border=\"0\" alt=\"CIMG0638\" width=\"240\" height=\"180\" /></a></p>\n<p>Christopher is also aware of Obama's mixed parentage, an American and a non-American, and that this is a trait the two share. For now anyway, Christopher wants to follow in Obama's footsteps. His career goal of the moment is to become President of the United States, and as a natural born US citizen, he meets the basic qualification. His previous career goal was Chief Designer for LEGO Group in Denmark, and maybe he will find a way to combine the two goals.</p>\n<p><a title=\"View 'CIMG0639' on Flickr.com\" href=\"http://www.flickr.com/photos/51035717986@N01/3006137696\"><img src=\"http://farm4.static.flickr.com/3279/3006137696_62a152d1b9_m.jpg\" border=\"0\" alt=\"CIMG0639\" width=\"240\" height=\"180\" /></a></p>"},"next":{"fields":{"title":"If I Had A Pony","date":"29 Oct 2008","slug":"/2008-10-29-if-i-had-a-pony/"}},"previous":{"fields":{"title":"Expansion","date":"11 Nov 2008","slug":"/2008-11-11-expansion/"}}},{"node":{"fields":{"title":"If I Had A Pony","date":"29 Oct 2008","slug":"/2008-10-29-if-i-had-a-pony/","tags":"Y2008"},"html":"<p><a href=\"http://lumma.de/eintrag.php?id=3895\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/10/worldvote2008.jpg\" alt=\"worldvote2008.jpg\" border=\"0\" width=\"225\" height=\"99\" align=\"right\" /></a>I'm pretty amused by things like the <a href=\"http://www.economist.com/vote2008/index.cfm\">Global Electoral College at the Economist</a>, which polls people people around the world asking how they would vote in the US election. Of course the results are gong to look like <a href=\"http://lumma.de/eintrag.php?id=3895\">this map from lumma.de</a>. It's like asking little kids what they would do if they had a pony... the answers may be cute but not particularly useful.</p>\n<p>I suspect the truth is actually closer to <a href=\"http://www.spiegel.de/international/world/0,1518,586770,00.html\">this interview with Robert Kagen</a> <a href=\"http://www.eamonn.com/2008/10/europe_exactly.htm\">(from Rainy Day)</a>;</p>\n<blockquote><p>SPIEGEL: An overwhelming majority of Europeans want to see Barack Obama become president...</p>\n<p>KAGAN: Yes, of the United States, although they would never elect someone like that in their own countries.</p></blockquote>"},"next":{"fields":{"title":"PapaScott for Obama","date":"28 Oct 2008","slug":"/2008-10-28-papascott-for-obama/"}},"previous":{"fields":{"title":"Yes We Can in 2036","date":"05 Nov 2008","slug":"/2008-11-05-yes-we-can-in-2036/"}}},{"node":{"fields":{"title":"PapaScott for Obama","date":"28 Oct 2008","slug":"/2008-10-28-papascott-for-obama/","tags":"Y2008"},"html":"<p>I haven't blogged much about this year's presidential campaign. But I'm going to put my money where my mouth is.</p>\n<p>I've just donated one month's advertising proceeds from PapaScott to the <a href=\"http://www.barackobama.com/\">Obama</a> campaign. My Text Link Ads are fully booked, so it's not an insignificant amount.</p>\n<p>However, it's not as significant as a single vote. If you are eligible to vote in this election, please do so.</p>"},"next":{"fields":{"title":"Easy Morning in Words and Pictures","date":"22 Oct 2008","slug":"/2008-10-22-easy-morning-in-words-and-pictures/"}},"previous":{"fields":{"title":"If I Had A Pony","date":"29 Oct 2008","slug":"/2008-10-29-if-i-had-a-pony/"}}}],"pathPrefix":"","first":false,"last":false,"index":68,"pageCount":1101}}

